@import url("https://fonts.googleapis.com/css2?family=Pacifico&display=swap");

.App {
  height: 90vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
}

.heading {
  font-family: "Pacifico", cursive;
  font-size: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.container {
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.middle,
.right {
  width: 120px;
}

.switch {
  padding: 5px;
  margin-top: 25px;
  background-color: rgb(226, 252, 184);
  border-radius: 50%;
  cursor: pointer;
}
.result {
  box-sizing: border-box;
  /* width: 800px; */
  padding-left: 30px;
}
.button {
  width: 100px;
  height: 30px;
  font-weight: bold;
  font-size: 20px;
  border: 2px solid forestgreen;
  background-color: rgb(226, 252, 184);
  cursor: pointer;
}
.p,
h3,
button,
switch {
  color: forestgreen;
}
.p {
  font-size: 30px;
}
